<template>
  <div class="cover_box" id="cover_box" @click="to_this_works">
    <div
      class="img_box"
      id="img_box"
      :style="{ width: img_width, height: img_height }"
    >
      <img
        class="cover_img"
        id="cover_img"
        :style="{ width: img_width, height: img_height }"
        :src="cover_src"
      />
    </div>

    <div class="cover_title" id="cover_title">{{ cover_title }}</div>

    <div class="tag_list" id="tag_list">
      <div class="tag" id="tag" v-for="(tag, index) of tag_list" :key="tag">{{ tag }}{{ index === tag_list.length - 1 ? "" : ", " }}</div>
    </div>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div {
  font-family: MyriadVariableConcept-Roman;
  color: #37383d;
}

/* div.cover_box {
    padding: 50rem;
} */

div.img_box {
  overflow: hidden;
}

img.cover_img {
  object-fit: cover;
  /* transform: scale(1); */
}

div.cover_title {
  padding-top: 30rem;
  color: #251714;
  font-family: FuturaLT-Heavy;
  font-size: 30rem;
  text-transform: uppercase;
  line-height: 1;
}

div.tag_list {
  padding-top: 10rem;
  display: flex;
  flex-direction: row;
}

div.tag {
  display: inline-block;
  font-size: 20rem;
  text-transform: uppercase;
  line-height: 1;
  white-space:pre-wrap;
}
</style>


<script>
import anime from "../../../../modules/anime";

export default {
  name: "WorksCover",
  props: [
    "id",
    "cover_name",
    "img_width",
    "img_height",
    "cover_src",
    "cover_title",
    "tag_list",
  ],
  data() {
    return {};
  },
  beforeMount() {},
  mounted() {
    window.onresize = () => {
      return (() => {
        this.refresh_root_size();
      })();
    };
  },
  destroyed() {},
  methods: {
    refresh_root_size() {
      let static_piexl = 960;
      let width =
        document.documentElement.clientWidth || document.body.clientWidth;
      if (width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize =
        document.documentElement.clientWidth / 1920 + "px";
    },
    to_this_works() {
       let that = this;
      setTimeout(function () {
        that.$router.push("/Works/"+that.id);
      }, 0);
    },
  },
};
</script>



